====================================================================

 ***/

body {
	font-size: 15px;
	color: #777777;
	line-height: 1.8em;
	font-weight: 400;
	background: #ffffff;
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center top;
	-webkit-font-smoothing: antialiased;
	font-family: 'Open Sans', sans-serif;
}

.Total{
	background-color: #F0F5F9;
}

a {
	text-decoration: none;
	cursor: pointer;
}

.no-padding {
	padding: 0px !important;
}

a:hover,
a:focus,
a:visited {
	text-decoration: none;
	outline: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	position: relative;
	font-weight: normal;
	margin: 0px;
	background: none;
	line-height: 1.6em;
	color: #000;
}

input,
button,
select,
textarea {}

button:focus {
	outline: none;
}

p {
	position: relative;
	line-height: 1.8em;
	color: #848484;
}
footer{
