.header-top {
	position: relative;
	font-size: 13px;
	border-bottom: 1px solid;
	border-color:rgba(188,201,210,0.3);
	padding:5px 0;
}

.header-top.grey {
    background: #ffffff;
    max-width: 1256px;
    margin: 0 auto;
}

.header-top.black-bg {
	background: #222;
}

.header-top.transparent {
	border: 0px;
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
}

.header-top.transparent .links-nav li {
    color: #fff;
    border: 0px !important;
}

.header-top.transparent .links-nav li .fa {
    color: #fff;
}

.header-top.transparent .social-links a {
    color: #fff;
    border:  0px !important;
}

.header-top a {
	color: #ffffff;
	transition: all 300ms ease;
}


.header-top.light .links-nav li {
	color: #d4dae3;
	border-color: rgba(255, 255, 255, 0.3);
}

.header-top .links-nav li:last-child {
	border-right: 1px solid #ffffff75;
}

.header-top.light .links-nav li:last-child {
	border-color: rgba(255, 255, 255, 0.3);
}

.header-top .top-right .links-nav li {
	margin-right: 0px;
	margin-left: 25px;
}

.header-top .links-nav li a {
	position: relative;
	line-height: 26px;
	font-size: 13px;
	color: #000;
	font-weight: 400;
	transition: .5s;
}

.header-top.black-bg .links-nav li a {
	color: #fff;
}

.header-top .links-nav li a:hover {
	color: #e6202d;
	transition: .5s;
}

.header-top .links-nav li .fa {
	padding-right: 8px;
	font-size: 16px;
	color: #ffffff;
}

.header-top .link-btn,
.header-top .text,
.header-top .social-links {
	display: inline-block;
}

.header-top .text,
.header-top .link-btn {
	margin-left: 25px;
}

.header-top .text {
	color: #111;
}

.header-top .social-links a {
	position: relative;
	display: inline-block;
	font-size: 24px;
	font-weight: normal;
	color: #78909C;
	padding: 0px 16px;
	line-height: 45px;
	border-left: 1px solid #ffffff75;
}

.header-top.light .social-links a {
	color: #d4dae3;
}

.header-top .social-links a:last-child {
	border-right: 1px solid #ffffff75;
}

.header-top.light .social-links a,
.header-top.light .social-links a:last-child {
	border-color: rgba(255, 255, 255, 0.3);
}

.header-top a:hover {
	color: #0084F8;
}
.logo {
