.sequence .popup {
  position: relative;
  display: inline-block;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

/* The actual popup */
.sequence .popup .popupbox {
    visibility: hidden;
    width: 120px;
    height: 47px;
    background-color: #fff;
    border: 1px solid #BCC9D2;
    border-radius: 4px;
    color: #fff;
    display: flex;
    justify-content: space-around;
    align-items: center;
    position: absolute;
    z-index: 1;
    top: 38px;
    right: 0;
}



/* Toggle this class - hide and show the popup */
.sequence .popup .show {
  visibility: visible;
  -webkit-animation: fadeIn 1s;
  animation: fadeIn 1s;
}

/* Add animation (fade in the popup) */
@-webkit-keyframes fadeIn {
  from {opacity: 0;} 
  to {opacity: 1;}
}

@keyframes fadeIn {
  from {opacity: 0;}
  to {opacity:1 ;}
}
